import React, { useEffect } from "react";
import "./Card.css";
import Aos from "aos";
import "aos/dist/aos.css";
function Cards({ title, description, mainImage, iconImage }) {
  useEffect(() => {
    Aos.init({ duration: 1000 });
  });

  return (
    <div className="card">
      <div className="card_imageContainer">
        <div className="upper_image">
          <img data-aos="flip-up" src={mainImage} />
        </div>
        <div className="icon_image">
          <img src={iconImage} alt="" />
        </div>
      </div>
      <div className="card_content">
        <h3>{title}</h3>
        <p data-aos="fade-right" className="component_description">
          {description}
        </p>
      </div>
    </div>
  );
}

export default Cards;
